* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
    background: linear-gradient(
            90deg,
            #370617 0%,
            #DC2F02 100%
    );
}

.todoApp {
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 35rem;
    min-height: 40rem;
    background: #03071E;
    text-align: center;
    margin: 8rem auto;
    border-radius: .8rem;
    padding-bottom: 2rem;
    box-shadow: .2rem .2rem 1.25rem .2rem #000;
}

h1 {
    margin: 2rem 0;
    color: #fff;
    font-size: 1.5rem;
}

.complete {
    text-decoration: line-through;
    opacity: 0.4;
}

.todoForm {
    margin-bottom: 2rem;
}

.todoInput {
    padding: .875rem 2rem .875rem 1rem;
    border-radius: 4px 0 0 4px;
    border: .125rem solid #E85D04;
    outline: none;
    width: 20rem;
    background: transparent;
    color: #fff;
}

.todoInput::placeholder {
    color: #e2e2e2;
}

.todoBtn {
    padding: 1rem;
    border: none;
    border-radius: 0 .25rem .25rem 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            #E85D04 0%,
            #FFBA08 100%
    );
    color: #fff;
    font-weight: bold;
    text-transform: capitalize;
}

.todoInput.edit {
    border: .125rem solid #9D0208;
}

.todoBtn.edit {
    background: linear-gradient(
            90deg,
            #9D0208 0%,
            #E85D04 100%
    );
    padding: 1rem 1.375rem;
}

.todoBtn:disabled {
    background: linear-gradient(
            90deg,
            #E85D0480 0%,
            #FFBA0880 100%
    );
}

.todoContainer {
    display: flex;
    flex-direction: row;
    position: relative;
}

.todoRow {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: .5rem auto;
    color: #fff;
    background: linear-gradient(
            90deg,
            #ef233c 0%,
            #d90429 100%
    );

    padding: 1rem;
    border-radius: .3rem;
    width: 90%;
}

.todoRow:nth-child(6n + 1) {
    background: linear-gradient(
            90deg,
            #e36414 0%,
            #fb8b24 100%
    );
}

.todoRow:nth-child(6n + 2) {
    background: linear-gradient(
            90deg,
            #aacc00 0%,
            #ffff3f 100%
    );
}

.todoRow:nth-child(6n + 3) {
    background: linear-gradient(
            90deg,
            #f72585 0%,
            #f9844a 100%
    );
}

.todoRow:nth-child(6n + 4) {
    background: linear-gradient(
            90deg,
            #480ca8 0%,
            #b5179e 100%
    );
}

.todoRow:nth-child(6n + 5) {
    background: linear-gradient(
            90deg,
            #38b000 0%,
            #9ef01a 100%
    );
}

.todoRowText {
    width: 90%;
    display: flex;
    align-items: flex-start;
}

.icons {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    cursor: pointer;
}

.deleteIcon {
    margin-right: .3rem;
    color: #fff;
}

.editIcon {
    color: #fff;
}

.errorMessage {
    display: inline-block;
    background: red;
    color: #fff;
    text-align: center;
    margin: .5rem auto 0;
    padding: .2rem;
    border: .15rem solid #fff;
    border-radius: 1rem;
}